import ProfileCard from "./components/UserProfileCard/index";
import ProfileEditPanel from "./components/ProfileEditPanel";
import { deepClone } from '@/utils/utils';
export default {
  components: {
    ProfileCard: ProfileCard,
    ProfileEditPanel: ProfileEditPanel
  },
  data: function data() {
    return {
      profileData: {},
      profileEditData: {}
    };
  },
  created: function created() {
    this.init();
  },
  methods: {
    init: function init() {
      this.profileData = deepClone(this.$store.state.user.selfInfo);
      this.profileEditData = deepClone(this.$store.state.user.selfInfo);
    }
  }
};